html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0; }

.headerofsite {
  display: block;
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(45deg, #EECFBA, #C5DDE8); }
  .headerofsite .h2 {
    position: absolute;
    color: white;
    top: 45%;
    width: 100%; }
    .headerofsite .h2 h2 {
      color: #000; }

section {
  position: relative;
  height: 100%; }
  section h2.text-center {
    color: #000;
    border: none;
    text-shadow: none;
    margin: 50px 0 35px; }
  section div.container {
    display: block;
    position: relative; }
    section div.container div.row {
      display: block;
      position: relative; }
      section div.container div.row .row {
        flex-direction: row;
        justify-content: center !important; }
        section div.container div.row .row li {
          list-style: none;
          align-items: center;
          padding: 15px; }
          section div.container div.row .row li a {
            text-decoration: none;
            color: forestgreen; }
      section div.container div.row div.layout {
        position: absolute;
        width: 100%;
        height: 80vh;
        padding: 15px;
        overflow: hidden; }
        section div.container div.row div.layout div.free-wall {
          width: 100%;
          height: 100%; }
          section div.container div.row div.layout div.free-wall div.brick img {
            max-width: 300px; }

@keyframes start {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="init"] {
            display: none; }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="start"] {
            display: block;
            animation: start 0.5s; }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="move"] {
            transition: top 0.5s, left 0.5s, width 0.5s, height 0.5s; }

ul.controls {
  font-size: 15px;
  margin: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center; }
  ul.controls li {
    text-transform: uppercase;
    position: relative;
    list-style: none;
    padding: 10px;
    cursor: pointer;
    transition: background 150ms;
    color: #28c700; }
  ul.controls li.active {
    color: black; }

.galery_container {
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr)); }
  .galery_container .mix {
    display: block;
    position: relative;
    box-sizing: content-box;
    overflow: hidden;
    margin: 12px 0; }
    .galery_container .mix img {
      box-sizing: content-box;
      overflow: hidden;
      width: 100%;
      z-index: 5; }
    .galery_container .mix .filter1 {
      visibility: hidden;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: 0;
      position: absolute;
      background: rgba(245, 250, 255, 0.8);
      z-index: 10; }
      .galery_container .mix .filter1 .border_portfolio {
        display: block;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: 0;
        position: absolute;
        border: #a8abae solid 1px;
        margin: 15px 20px; }
        .galery_container .mix .filter1 .border_portfolio h3 {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          align-items: center;
          font-size: 1rem;
          text-align: center;
          color: black;
          z-index: 11; }
    .galery_container .mix:hover > .filter1 {
      visibility: visible; }
